/* General layout and body styling */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f8f9fa;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

h1 {
  color: #343a40;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 20px;
}

/* Container for the entire app */
.todo-container {
  background-color: white;
  width: 500px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Form styling for adding todos */
form {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 20px;
}

form input {
  padding: 12px;
  margin-bottom: 10px;
  border: 1px solid #ced4da;
  border-radius: 6px;
  font-size: 16px;
}

form button {
  padding: 12px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #0056b3;
}

/* List of todos */
ul {
  list-style: none;
  padding: 0;
  width: 100%;
}

li {
  padding: 15px;
  background-color: #f1f1f1;
  margin-bottom: 10px;
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #dee2e6;
  transition: background-color 0.3s ease;
}

li:hover {
  background-color: #e9ecef;
}

li.completed {
  text-decoration: line-through;
  color: gray;
}

li strong {
  font-size: 1.1rem;
}

li button {
  padding: 6px 12px;
  margin-left: 10px;
  font-size: 14px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

li button.complete {
  background-color: #28a745;
}

li button.complete:hover {
  background-color: #218838;
}

li button.delete:hover {
  background-color: #c82333;
}

/* Responsive design */
@media (max-width: 600px) {
  .todo-container {
    width: 90%;
  }

  li {
    flex-direction: column;
    align-items: flex-start;
  }
}
